<template>
    <div>
        <div class="row">
            <div class="card mx-auto">
                <div class="card-header text-white bg-primary">Register</div>
                <div class="card-body">
                    <form @submit.prevent='registerUser'>
                        <div class="form-group">
                            <label for="username">Username</label>
                            <input 
                                id="username"
                                type="text" 
                                placeholder="Username" 
                                name="username" 
                                v-model="username" 
                                class="form-control"
                            >
                        </div>
                        <div class="form-group">
                            <label for="name">Name</label>
                            <input 
                                id="name"
                                type="text" 
                                placeholder="Name" 
                                name="name" 
                                v-model="name" 
                                class="form-control"
                            >
                        </div>
                        <div class="form-group">
                            <label for="email">Email</label>
                            <input 
                                id="email"
                                type="text" 
                                placeholder="Email" 
                                name="email" 
                                v-model="email" 
                                class="form-control"
                            >
                        </div>
                        <div class="form-group">
                            <label for="Password">Password</label>
                            <input 
                                id="password"
                                type="password" 
                                placeholder="Password" 
                                name="password" 
                                v-model="password" 
                                class="form-control"
                            >
                        </div>
                        <div class="form-group">
                            <label for="confirm_password">Confirm Password</label>
                            <input 
                                id="confirm_password"
                                type="password" 
                                placeholder="Confirm Password" 
                                name="confirm_password" 
                                v-model="confirm_password" 
                                class="form-control"
                            >
                        </div>
                        <input type="submit" value="Register">
                        <router-link to="/login" class="card-link">Already have an account?</router-link>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
    data: () => ({
        username: '',
        password: '',
        confirm_password: '',
        name: '',
        email: ''
    }),
    methods: {
        ...mapActions([
            'register'
        ]),
        registerUser() {
            let user = {
                username: this.username,
                password: this.password,
                confirm_password: this.confirm_password,
                name: this.name,
                email: this.email
            }
            this.register(user)
                .then(res => {
                    if (res.data.success) {
                        this.$router.push('login');
                    }
                })
                .catch(err => console.log(err))
        }
    }
}
</script>